/* Gamified Reward Popup Styles */
@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes scaleIn {
  from { transform: scale(0.8); opacity: 0; }
  to { transform: scale(1); opacity: 1; }
}

@keyframes floatUp {
  from { transform: translateY(20px); opacity: 0; }
  to { transform: translateY(0); opacity: 1; }
}

@keyframes glowPulse {
  0% { box-shadow: 0 0 10px rgba(255, 215, 0, 0.5); }
  50% { box-shadow: 0 0 25px rgba(255, 215, 0, 0.8); }
  100% { box-shadow: 0 0 10px rgba(255, 215, 0, 0.5); }
}

@keyframes spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

.gamified-reward-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.85);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  animation: fadeIn 0.5s ease-out;
}

.gamified-reward-container {
  position: relative;
  width: 90%;
  max-width: 800px;
  background: linear-gradient(135deg, #1a1a2e, #16213e);
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 0 30px rgba(0, 0, 0, 0.7);
  animation: scaleIn 0.5s ease-out;
  
  &.lost {
    border: 3px solid #ff3333;
    box-shadow: 0 0 30px rgba(255, 51, 51, 0.5);
    background: linear-gradient(135deg, #2a1a1a, #2e1616);
  }
  
  &.uncommon {
    border: 3px solid #66cc33;
    box-shadow: 0 0 30px rgba(102, 204, 51, 0.5);
    background: linear-gradient(135deg, #1a2a1a, #162e16);
  }
  
  &.rare {
    border: 3px solid #3399ff;
    box-shadow: 0 0 30px rgba(51, 153, 255, 0.5);
    background: linear-gradient(135deg, #1a1a2a, #16162e);
  }
  
  &.epic {
    border: 3px solid #cc33ff;
    box-shadow: 0 0 30px rgba(204, 51, 255, 0.5);
    background: linear-gradient(135deg, #2a1a2a, #2e162e);
  }
  
  &.legendary {
    border: 3px solid #ffcc00;
    box-shadow: 0 0 30px rgba(255, 204, 0, 0.7);
    background: linear-gradient(135deg, #2a2a1a, #2e2e16);
  }
}

@keyframes imageReveal {
  0% { opacity: 0; transform: scale(1.1); filter: blur(10px); }
  100% { opacity: 1; transform: scale(1); filter: blur(0); }
}

.gamified-reward-image {
  width: 100%;
  height: 400px;
  object-fit: cover;
  border-bottom: 2px solid rgba(255, 126, 0, 0.5);
  animation: imageReveal 1s ease-out forwards;
  
  .lost & {
    border-bottom: 2px solid rgba(255, 51, 51, 0.7);
    filter: contrast(1.2) saturate(1.1);
  }
  
  .uncommon & {
    border-bottom: 2px solid rgba(102, 204, 51, 0.7);
    filter: contrast(1.1) saturate(1.2);
  }
  
  .rare & {
    border-bottom: 2px solid rgba(51, 153, 255, 0.7);
    filter: contrast(1.2) saturate(1.3);
  }
  
  .epic & {
    border-bottom: 2px solid rgba(204, 51, 255, 0.7);
    filter: contrast(1.3) saturate(1.4);
  }
  
  .legendary & {
    border-bottom: 2px solid rgba(255, 204, 0, 0.7);
    filter: contrast(1.4) saturate(1.5) brightness(1.1);
  }
}

