@keyframes numberCountUp {
  0% { opacity: 0; transform: scale(0.5); }
  50% { opacity: 1; transform: scale(1.2); }
  100% { opacity: 1; transform: scale(1); }
}

@keyframes shimmer {
  0% { background-position: -200% 0; }
  100% { background-position: 200% 0; }
}

.gamified-reward-amount {
  font-size: 48px;
  font-weight: 800;
  color: transparent;
  background: linear-gradient(90deg, #fff, #ffcc00, #ff7e00, #ffcc00, #fff);
  background-size: 200% 100%;
  -webkit-background-clip: text;
  background-clip: text;
  text-shadow: 0 0 15px rgba(255, 215, 0, 0.7);
  display: inline-block;
  padding: 15px 30px;
  border-radius: 12px;
  position: relative;
  z-index: 1;
  animation: numberCountUp 1.5s ease-out forwards, shimmer 3s infinite linear;
  
  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(135deg, rgba(255, 126, 0, 0.2), rgba(255, 215, 0, 0.2));
    border-radius: 12px;
    z-index: -1;
    border: 2px solid rgba(255, 215, 0, 0.5);
    box-shadow: 0 0 20px rgba(255, 215, 0, 0.5), inset 0 0 10px rgba(255, 215, 0, 0.3);
  }
  
  .lost & {
    background: linear-gradient(90deg, #fff, #ff6666, #ff3333, #ff6666, #fff);
    background-size: 200% 100%;
    -webkit-background-clip: text;
    background-clip: text;
    text-shadow: 0 0 15px rgba(255, 51, 51, 0.7);
    
    &::before {
      background: linear-gradient(135deg, rgba(255, 51, 51, 0.2), rgba(255, 102, 102, 0.2));
      border: 2px solid rgba(255, 51, 51, 0.5);
      box-shadow: 0 0 20px rgba(255, 51, 51, 0.5), inset 0 0 10px rgba(255, 51, 51, 0.3);
    }
  }
  
  .uncommon & {
    background: linear-gradient(90deg, #fff, #99ff66, #66cc33, #99ff66, #fff);
    background-size: 200% 100%;
    -webkit-background-clip: text;
    background-clip: text;
    text-shadow: 0 0 15px rgba(102, 204, 51, 0.7);
    
    &::before {
      background: linear-gradient(135deg, rgba(102, 204, 51, 0.2), rgba(153, 255, 102, 0.2));
      border: 2px solid rgba(102, 204, 51, 0.5);
      box-shadow: 0 0 20px rgba(102, 204, 51, 0.5), inset 0 0 10px rgba(102, 204, 51, 0.3);
    }
  }
  
  .rare & {
    background: linear-gradient(90deg, #fff, #66ccff, #3399ff, #66ccff, #fff);
    background-size: 200% 100%;
    -webkit-background-clip: text;
    background-clip: text;
    text-shadow: 0 0 15px rgba(51, 153, 255, 0.7);
    
    &::before {
      background: linear-gradient(135deg, rgba(51, 153, 255, 0.2), rgba(102, 204, 255, 0.2));
      border: 2px solid rgba(51, 153, 255, 0.5);
      box-shadow: 0 0 20px rgba(51, 153, 255, 0.5), inset 0 0 10px rgba(51, 153, 255, 0.3);
    }
  }
  
  .epic & {
    background: linear-gradient(90deg, #fff, #ff66ff, #cc33ff, #ff66ff, #fff);
    background-size: 200% 100%;
    -webkit-background-clip: text;
    background-clip: text;
    text-shadow: 0 0 15px rgba(204, 51, 255, 0.7);
    
    &::before {
      background: linear-gradient(135deg, rgba(204, 51, 255, 0.2), rgba(255, 102, 255, 0.2));
      border: 2px solid rgba(204, 51, 255, 0.5);
      box-shadow: 0 0 20px rgba(204, 51, 255, 0.5), inset 0 0 10px rgba(204, 51, 255, 0.3);
    }
  }
  
  .legendary & {
    background: linear-gradient(90deg, #fff, #ffee66, #ffcc00, #ffee66, #fff);
    background-size: 200% 100%;
    -webkit-background-clip: text;
    background-clip: text;
    text-shadow: 0 0 15px rgba(255, 204, 0, 0.7);
    
    &::before {
      background: linear-gradient(135deg, rgba(255, 204, 0, 0.3), rgba(255, 238, 102, 0.3));
      border: 2px solid rgba(255, 204, 0, 0.6);
      box-shadow: 0 0 25px rgba(255, 204, 0, 0.6), inset 0 0 15px rgba(255, 204, 0, 0.4);
    }
  }
}

.gamified-reward-currency {
  font-size: 18px;
  font-weight: bold;
  color: #ff7e00;
  margin-top: 5px;
}

@keyframes buttonPulse {
  0% { box-shadow: 0 0 0 0 rgba(255, 126, 0, 0.7); }
  70% { box-shadow: 0 0 0 15px rgba(255, 126, 0, 0); }
  100% { box-shadow: 0 0 0 0 rgba(255, 126, 0, 0); }
}

@keyframes buttonAppear {
  0% { opacity: 0; transform: translateY(20px); }
  100% { opacity: 1; transform: translateY(0); }
}

.gamified-reward-buttons {
  display: flex;
  justify-content: center;
  gap: 25px;
  margin-top: 40px;
}

.gamified-reward-button {
  padding: 15px 35px;
  border-radius: 30px;
  font-weight: bold;
  font-size: 16px;
  text-transform: uppercase;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  position: relative;
  overflow: hidden;
  letter-spacing: 1px;
  
  &::before {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    width: 0;
    height: 0;
    background: rgba(255, 255, 255, 0.2);
    border-radius: 50%;
    transform: translate(-50%, -50%);
    transition: all 0.5s ease;
    z-index: 1;
  }
  
  &:hover::before {
    width: 300px;
    height: 300px;
  }
  
  &.claim {
    background: linear-gradient(135deg, #ff7e00, #ffb700);
    color: #fff;
    border: none;
    box-shadow: 0 4px 15px rgba(255, 126, 0, 0.4);
    animation: buttonAppear 0.5s ease-out 0.5s backwards, buttonPulse 2s infinite;
    
    &:hover {
      transform: translateY(-5px) scale(1.05);
      box-shadow: 0 10px 25px rgba(255, 126, 0, 0.6);
    }
    
    &:active {
      transform: translateY(-2px) scale(0.98);
    }
    
    .lost & {
      background: linear-gradient(135deg, #ff3333, #ff6666);
      box-shadow: 0 4px 15px rgba(255, 51, 51, 0.4);
      
      &:hover {
        box-shadow: 0 10px 25px rgba(255, 51, 51, 0.6);
      }
    }
    
    .uncommon & {
      background: linear-gradient(135deg, #66cc33, #99ff66);
      box-shadow: 0 4px 15px rgba(102, 204, 51, 0.4);
      
      &:hover {
        box-shadow: 0 10px 25px rgba(102, 204, 51, 0.6);
      }
    }
    
    .rare & {
      background: linear-gradient(135deg, #3399ff, #66ccff);
      box-shadow: 0 4px 15px rgba(51, 153, 255, 0.4);
      
      &:hover {
        box-shadow: 0 10px 25px rgba(51, 153, 255, 0.6);
      }
    }
    
    .epic & {
      background: linear-gradient(135deg, #cc33ff, #ff66ff);
      box-shadow: 0 4px 15px rgba(204, 51, 255, 0.4);
      
      &:hover {
        box-shadow: 0 10px 25px rgba(204, 51, 255, 0.6);
      }
    }
    
    .legendary & {
      background: linear-gradient(135deg, #ffcc00, #ffee66);
      box-shadow: 0 4px 15px rgba(255, 204, 0, 0.4);
      
      &:hover {
        box-shadow: 0 10px 25px rgba(255, 204, 0, 0.6);
      }
    }
  }
  
  &.dismiss {
    background: rgba(0, 0, 0, 0.3);
    color: #ddd;
    border: 2px solid rgba(255, 255, 255, 0.3);
    backdrop-filter: blur(5px);
    animation: buttonAppear 0.5s ease-out 0.7s backwards;
    
    &:hover {
      color: #fff;
      border-color: #fff;
      background: rgba(255, 255, 255, 0.15);
      transform: translateY(-3px);
      box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
    }
    
    &:active {
      transform: translateY(-1px);
    }
  }
  
  span {
    position: relative;
    z-index: 2;
  }
}

.gamified-reward-claim-btn {
  padding: 12px 30px;
  font-size: 16px;
  font-weight: bold;
  text-transform: uppercase;
  background: linear-gradient(135deg, #ff7e00, #ffb700);
  border: none;
  border-radius: 50px;
