  }
  
  .uncommon & {
    background: linear-gradient(135deg, #66cc33, #99ff66);
    box-shadow: 0 5px 15px rgba(102, 204, 51, 0.4);
  }
  
  .rare & {
    background: linear-gradient(135deg, #3399ff, #66ccff);
    box-shadow: 0 5px 15px rgba(51, 153, 255, 0.4);
  }
  
  .epic & {
    background: linear-gradient(135deg, #cc33ff, #ff66ff);
    box-shadow: 0 5px 15px rgba(204, 51, 255, 0.4);
  }
  
  .legendary & {
    background: linear-gradient(135deg, #ffcc00, #ffee66);
    box-shadow: 0 5px 15px rgba(255, 204, 0, 0.6);
  }
