/* Loading animation */
@keyframes flicker {
  0% { opacity: 0.7; }
  25% { opacity: 1; }
  50% { opacity: 0.7; }
  75% { opacity: 0.9; }
  100% { opacity: 0.7; }
}

@keyframes progress {
  0% { width: 0%; }
  20% { width: 20%; }
  40% { width: 38%; }
  60% { width: 59%; }
  80% { width: 79%; }
  100% { width: 100%; }
}

@keyframes typing {
  from { width: 0; }
  to { width: 100%; }
}

@keyframes flame {
  0% { transform: scale(1) rotate(0deg); opacity: 0.8; }
  25% { transform: scale(1.1) rotate(5deg); opacity: 1; }
  50% { transform: scale(0.9) rotate(-5deg); opacity: 0.8; }
  75% { transform: scale(1.05) rotate(3deg); opacity: 0.9; }
  100% { transform: scale(1) rotate(0deg); opacity: 0.8; }
}

.loading-theme {
  background: linear-gradient(135deg, #1a0f00, #2d1500) !important;
  box-shadow: 0 0 40px rgba(255, 69, 0, 0.4) !important;
  border: 2px solid rgba(255, 126, 0, 0.5) !important;
}

.gamified-loading-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 50px 0;
  height: 450px;
  position: relative;
  overflow: hidden;
}

.gamified-loading-effect {
  position: relative;
  width: 120px;
  height: 120px;
  margin-bottom: 30px;
}

.gamified-loading-spinner {
  width: 120px;
  height: 120px;
  border: 10px solid rgba(255, 126, 0, 0.1);
  border-radius: 50%;
  border-top: 10px solid #ff7e00;
  border-right: 10px solid transparent;
  border-bottom: 10px solid #ff7e00;
  border-left: 10px solid transparent;
  animation: spin 1.5s linear infinite;
  position: absolute;
  top: 0;
  left: 0;
  box-shadow: 0 0 30px rgba(255, 126, 0, 0.5);
}

.gamified-loading-flames {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 80px;
  height: 80px;
  background: radial-gradient(circle, rgba(255,126,0,0.8) 0%, rgba(255,69,0,0.4) 60%, rgba(255,69,0,0) 100%);
  border-radius: 50%;
  animation: flame 3s ease-in-out infinite;
  filter: blur(5px);
  z-index: -1;
}

.gamified-loading-text {
  font-size: 28px;
  font-weight: bold;
  margin-bottom: 25px;
  text-transform: uppercase;
  color: #ff7e00;
  text-shadow: 0 0 15px rgba(255, 126, 0, 0.7);
  letter-spacing: 3px;
  animation: flicker 3s infinite;
}

.gamified-loading-progress {
  width: 80%;
  height: 8px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 10px;
  margin-bottom: 25px;
  overflow: hidden;
}

.gamified-loading-progress-bar {
  height: 100%;
  background: linear-gradient(90deg, #ff7e00, #ffb700);
  border-radius: 10px;
  animation: progress 8s ease-in-out infinite;
  box-shadow: 0 0 10px rgba(255, 126, 0, 0.7);
}

.gamified-loading-subtext {
  font-size: 16px;
  color: #ccc;
  text-align: center;
  max-width: 80%;
  line-height: 1.6;
  position: relative;
}

.typing-animation {
  display: inline-block;
  overflow: hidden;
  white-space: nowrap;
  animation: typing 3.5s steps(40, end) 0.5s forwards;
  width: 0;
}

.gamified-loading-text {
  font-size: 24px;
  font-weight: bold;
  color: #ff7e00;
  margin-bottom: 10px;
  text-transform: uppercase;
}

.gamified-loading-subtext {
  font-size: 16px;
  color: #ccc;
  text-align: center;
  max-width: 400px;
  line-height: 1.5;
}

/* Tier-specific styles */
.gamified-reward-container.lost {
  background: linear-gradient(135deg, #1a1a2e, #16213e);
  border: 2px solid rgba(100, 100, 100, 0.5);
}

.gamified-reward-container.uncommon {
  background: linear-gradient(135deg, #1a1a2e, #16213e);
  border: 2px solid rgba(75, 175, 80, 0.5);
}

.gamified-reward-container.rare {
  background: linear-gradient(135deg, #1a1a2e, #16213e);
  border: 2px solid rgba(33, 150, 243, 0.5);
}

.gamified-reward-container.epic {
  background: linear-gradient(135deg, #1a1a2e, #16213e);
  border: 2px solid rgba(156, 39, 176, 0.5);
}

.gamified-reward-container.legendary {
  background: linear-gradient(135deg, #1a1a2e, #16213e);
  border: 2px solid rgba(255, 215, 0, 0.5);
}

// New Mission NFT Card Styles
.nft-mission-card {
  display: grid;
  grid-template-columns: 300px 1fr;
  gap: 2rem;
  background: rgba(#0a0a14, 0.9);
  border-radius: 20px;
  padding: 1.5rem;
  margin-bottom: 1rem;
  border: 2px solid rgba(#36caba, 0.2);
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
  
  &:hover {
    border-color: rgba(#36caba, 0.4);
    box-shadow: 0 10px 30px rgba(#36caba, 0.2);
    transform: translateY(-2px);
  }
  
  &.fire-type {
    border-color: rgba(#ff5e3a, 0.3);
    &:hover { border-color: rgba(#ff5e3a, 0.6); }
  }
  
  &.water-type {
    border-color: rgba(#4facfe, 0.3);
    &:hover { border-color: rgba(#4facfe, 0.6); }
  }
  
  &.earth-type {
    border-color: rgba(#43a047, 0.3);
    &:hover { border-color: rgba(#43a047, 0.6); }
  }
}

.nft-info-section {
  display: flex;
  gap: 1rem;
  
  .nft-image-container {
    position: relative;
    width: 120px;
    height: 120px;
    border-radius: 15px;
    overflow: hidden;
    
    .nft-image {
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 15px;
      transition: transform 0.3s ease;
    }
    
    .nft-level-badge {
      position: absolute;
      top: 8px;
      right: 8px;
      background: linear-gradient(135deg, #ffd700, #ff8c00);
      color: #000;
      padding: 0.3rem 0.6rem;
      border-radius: 12px;
      font-size: 0.8rem;
      font-weight: bold;
      box-shadow: 0 2px 10px rgba(#ffd700, 0.4);
      z-index: 10;
    }
  }
  
  .nft-details {
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    
    .nft-id {
      color: rgba(#fff, 0.7);
      font-size: 0.9rem;
      font-weight: 500;
    }
    
    .nft-name {
      color: #fff;
      font-size: 1.3rem;
      font-weight: bold;
      margin-bottom: 0.5rem;
    }
    
    .mission-status {
      display: flex;
      align-items: center;
      gap: 0.5rem;
      padding: 0.5rem 0.8rem;
      background: rgba(#ff8c00, 0.2);
      border-radius: 10px;
      border: 1px solid rgba(#ff8c00, 0.3);
      
      .status-icon {
        font-size: 1.2rem;
      }
      
      .status-text {
        display: flex;
        flex-direction: column;
        
        .status-label {
          color: #fff;
          font-size: 0.8rem;
          font-weight: bold;
        }
        
        .status-time {
          color: #ffd700;
          font-size: 0.7rem;
          font-weight: 500;
        }
      }
    }
    
    .reward-ready {
      display: flex;
      align-items: center;
      gap: 0.5rem;
      padding: 0.5rem 0.8rem;
      background: linear-gradient(135deg, rgba(#ffd700, 0.3), rgba(#ff8c00, 0.2));
      border-radius: 10px;
      border: 1px solid rgba(#ffd700, 0.5);
      animation: treasure-pulse 2s infinite;
      
      .reward-icon {
        font-size: 1.2rem;
        animation: bounce 1s infinite;
      }
      
      .reward-text {
        color: #ffd700;
        font-size: 0.9rem;
        font-weight: bold;
        text-shadow: 0 0 10px rgba(#ffd700, 0.5);
      }
    }
  }
}

.nft-actions-section {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  
  .action-group {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1rem;
    background: rgba(#1a1a2e, 0.6);
    border-radius: 12px;
    border: 1px solid rgba(#36caba, 0.2);
    
    .action-info {
      display: flex;
      flex-direction: column;
      gap: 0.3rem;
      
      .action-label {
        color: #fff;
        font-size: 1rem;
        font-weight: bold;
      }
      
      .action-cost {
        color: #36caba;
        font-size: 0.9rem;
        font-weight: 500;
      }
    }
  }
}

.action-button {
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 140px;
  padding: 0.8rem 1.2rem;
  border: none;
  border-radius: 12px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-weight: bold;
  position: relative;
  overflow: hidden;
  
  .button-content {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    position: relative;
    z-index: 2;
    
    .button-icon {
      font-size: 1.1rem;
    }
    
    .button-text {
      font-size: 0.9rem;
    }
  }
  
  &.feed-button {
    background: linear-gradient(135deg, #43a047, #66bb6a);
    color: #fff;
    box-shadow: 0 4px 15px rgba(#43a047, 0.3);
    
    &:hover:not(:disabled) {
      transform: translateY(-2px);
      box-shadow: 0 8px 25px rgba(#43a047, 0.5);
    }
  }
  
  &.mission-button {
    background: linear-gradient(135deg, #ff5722, #ff7043);
    color: #fff;
    box-shadow: 0 4px 15px rgba(#ff5722, 0.3);
    
    &:hover:not(:disabled) {
      transform: translateY(-2px);
      box-shadow: 0 8px 25px rgba(#ff5722, 0.5);
    }
  }
  
  &.claim-button {
    background: linear-gradient(135deg, #ffd700, #ff8c00);
    color: #000;
    box-shadow: 0 4px 15px rgba(#ffd700, 0.4);
    
    &.treasure-ready {
      position: relative;
      animation: treasure-ready-pulse 1.5s infinite;
      
      .treasure-glow {
        position: absolute;
        top: -2px;
        left: -2px;
        right: -2px;
        bottom: -2px;
        background: linear-gradient(135deg, #ffd700, #ff8c00);
        border-radius: 14px;
        z-index: 1;
        filter: blur(6px);
        opacity: 0.7;
        animation: treasure-glow 2s infinite;
      }
    }
    
    &:hover:not(:disabled) {
      transform: translateY(-3px) scale(1.05);
      box-shadow: 0 12px 35px rgba(#ffd700, 0.6);
    }
  }
  
  &:disabled {
    opacity: 0.6;
    cursor: not-allowed;
    transform: none !important;
  }
  
  &.loading {
    &::after {
      content: '';
      position: absolute;
      width: 20px;
      height: 20px;
      border: 2px solid rgba(#fff, 0.3);
      border-top: 2px solid #fff;
      border-radius: 50%;
      animation: spin 1s linear infinite;
    }
  }
}

.mission-cooldown {
  padding: 0.8rem 1.2rem;
  background: rgba(#ff9800, 0.1);
  border: 1px solid rgba(#ff9800, 0.3);
  border-radius: 12px;
  
  .cooldown-content {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    
    .cooldown-icon {
      font-size: 1.1rem;
    }
    
    .cooldown-text {
      color: #fff;
      font-size: 0.9rem;
      font-weight: bold;
    }
    
    .cooldown-time {
      color: #ff9800;
      font-size: 0.8rem;
      font-weight: 500;
    }
  }
}

.no-reward {
  padding: 0.8rem 1.2rem;
  background: rgba(#666, 0.1);
  border: 1px solid rgba(#666, 0.2);
  border-radius: 12px;
  
  .no-reward-text {
    color: rgba(#fff, 0.5);
    font-size: 0.9rem;
    font-style: italic;
  }
}

// Animations
@keyframes treasure-pulse {
  0%, 100% { 
    transform: scale(1);
    box-shadow: 0 0 20px rgba(#ffd700, 0.3);
  }
  50% { 
    transform: scale(1.02);
    box-shadow: 0 0 30px rgba(#ffd700, 0.6);
  }
}

@keyframes treasure-ready-pulse {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.03); }
}

@keyframes treasure-glow {
  0%, 100% { opacity: 0.7; }
  50% { opacity: 1; }
}

@keyframes bounce {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-5px); }
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

// Responsive Design
@media (max-width: 768px) {
  .nft-mission-card {
    grid-template-columns: 1fr;
    gap: 1rem;
    padding: 1rem;
  }
  
  .nft-info-section {
    .nft-image-container {
      width: 100px;
      height: 100px;
    }
  }
  
  .nft-actions-section {
    .action-group {
      flex-direction: column;
      align-items: stretch;
      gap: 0.8rem;
      
      .action-info {
        text-align: center;
      }
    }
  }
  
  .action-button {
    width: 100%;
    min-width: auto;
  }
}
